@import "../abstracts/mixins";

.ContactUs {
  padding: 5rem 10%;

  background: #f7f7f7;

  &__title {
    text-align: center;
    font-size: 3rem;
    padding: 1rem;
    color: #666;
  }
  &__container {
    padding: 2rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    border-radius: 0.5rem;
  }

  &__image {
    flex: 1 1 30rem;

    & img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      border-radius: 0.5rem;
    }
  }

  &__form {
    flex: 1 1 50rem;
    padding: 1rem;

    & button {
      @include mainButton;

      &:hover {
        transform: translateY(-0.2rem) scale(1.005);
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
      }
    }
  }

  &__inputs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    & input {
      padding: 1rem;
      margin: 1rem 0;
      font-size: 1.7rem;
      color: #333;
      text-transform: none;
      border: 0.1rem solid rgba(0, 0, 0, 0.3);
      border-radius: 0.5rem;
      width: 49%;
    }
  }

  &__message {
    & textarea {
      padding: 1rem;
      margin: 1rem 0;
      font-size: 1.7rem;
      color: #333;
      text-transform: none;
      border: 0.1rem solid rgba(0, 0, 0, 0.3);
      border-radius: 0.5rem;

      width: 100%;
      resize: none;
      height: 15rem;
    }
  }
}
