@import "./variables";

@mixin mainButton {
  display: inline-block;
  padding: 0.8rem 3rem;
  border: 0.2rem solid #14298c;
  color: $font-navy;
  background-color: #e7e9f3; //#ffe4e1;
  cursor: pointer;
  font-size: 1.7rem;
  font-weight: 700;
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  z-index: 0;
  margin: 1rem;
}

@mixin checkoutButton($background: transparent, $font-color: black) {
  display: inline-block;
  padding: 0.5rem 2rem;
  border: 0.2rem solid black;
  color: $font-color;
  background-color: $background;
  cursor: pointer;
  font-size: 1.7rem;
  font-weight: 700;
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  z-index: 0;
  margin: 1rem;
}
